body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: hidden;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.shadow-1px {    /*1px 阴影*/
  box-shadow: 1px 1px 2px 1px rgba(32,37,46,0.1);
}
.block-wrap {
  overflow: hidden;

}
.block {
  background: #fff;
  padding: 20px;
  margin-right: -20px;
  margin-bottom: 2px;
  height: calc(89vh - 68px);
  overflow-x: hidden;
  position: relative;
  overflow-y: scroll;
}
.block-title{
  margin: 20px 0;
  font-size: 18px;
  font-weight: bold;
}
@media only screen and (max-width: 1300px) {
  body {
    width: 1366px;
  }
}